// IndexedDB  
class RealEstateDB {
  constructor() {
    this.dbName = 'RealEstateDB';
    this.version = 1;
    this.storeName = 'realEstateData';
  }

  //  
  async openDB() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, this.version);
      
      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve(request.result);
      
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains(this.storeName)) {
          const store = db.createObjectStore(this.storeName, { keyPath: 'id' });
          store.createIndex('timestamp', 'timestamp', { unique: false });
        }
      };
    });
  }

  //  
  async saveData(data, timestamp) {
    const db = await this.openDB();
    const transaction = db.transaction([this.storeName], 'readwrite');
    const store = transaction.objectStore(this.storeName);
    
    const dataToStore = {
      id: 'current',
      data: data,
      timestamp: timestamp,
      lastUpdated: Date.now()
    };
    
    await store.put(dataToStore);
    console.log('IndexedDB   ');
  }

  //  
  async loadData() {
    try {
      const db = await this.openDB();
      const transaction = db.transaction([this.storeName], 'readonly');
      const store = transaction.objectStore(this.storeName);
      const request = store.get('current');
      
      return new Promise((resolve, reject) => {
        request.onsuccess = () => {
          const result = request.result;
          if (result && this.isCacheValid(result.lastUpdated)) {
            console.log('IndexedDB   ');
            resolve({
              data: result.data,
              timestamp: result.timestamp
            });
          } else {
            resolve(null);
          }
        };
        request.onerror = () => reject(request.error);
      });
    } catch (error) {
      console.error('IndexedDB   :', error);
      return null;
    }
  }

  //    (24)
  isCacheValid(lastUpdated) {
    const now = Date.now();
    const cacheAge = now - lastUpdated;
    const maxAge = 24 * 60 * 60 * 1000; // 24
    return cacheAge < maxAge;
  }

  //  
  async clearCache() {
    const db = await this.openDB();
    const transaction = db.transaction([this.storeName], 'readwrite');
    const store = transaction.objectStore(this.storeName);
    await store.clear();
    console.log('IndexedDB   ');
  }

  //    ( )
  async saveDataCompressed(data, timestamp) {
    const db = await this.openDB();
    const transaction = db.transaction([this.storeName], 'readwrite');
    const store = transaction.objectStore(this.storeName);
    
    //   (JSON.stringify  )
    const compressedData = JSON.stringify(data);
    
    const dataToStore = {
      id: 'current',
      data: compressedData,
      timestamp: timestamp,
      lastUpdated: Date.now(),
      compressed: true
    };
    
    await store.put(dataToStore);
    console.log('IndexedDB    ');
  }
  
  //   
  async loadDataCompressed() {
    try {
      const db = await this.openDB();
      const transaction = db.transaction([this.storeName], 'readonly');
      const store = transaction.objectStore(this.storeName);
      const request = store.get('current');
      
      return new Promise((resolve, reject) => {
        request.onsuccess = () => {
          const result = request.result;
          if (result && this.isCacheValid(result.lastUpdated)) {
            try {
              //  
              const decompressedData = result.compressed ? 
                JSON.parse(result.data) : result.data;
              
              resolve({
                data: decompressedData,
                timestamp: result.timestamp
              });
            } catch (e) {
              console.error('   :', e);
              resolve(null);
            }
          } else {
            resolve(null);
          }
        };
        request.onerror = () => reject(request.error);
      });
    } catch (error) {
      console.error('IndexedDB   :', error);
      return null;
    }
  }
}

export default new RealEstateDB();
